<template>
  <div class="form__counter">
    <button type="button" aria-label="  " v-on:click.prevent="currentValue--">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="currentValue" name="count">

    <button type="button" aria-label="  " v-on:click.prevent="currentValue++">
      <svg width="12" height="12" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script> 
  export default {
    props:['value'],
    computed: {
        currentValue: {
            get() {
              return this.value;
            },
            set(newValue) {
              if(newValue > 0) {
                this.changeValue(newValue);
              }
            }

        }
    },
    methods: {
        changeValue(newValue) {
            this.$emit('change-quantity', newValue);
        }
    },
  }
</script>
